#preview{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    background: #fff;
    z-index: 10;
    #content{
        .top{
            box-shadow: 5px 5px 5px rgba($color: #000000, $alpha: 0.5);
            padding: 10px;
            img{width: 120px;height: 142px;}
            display: flex;
            .right{
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                padding: 0 10px;
                p{font-size:16px;}
            }
        }
        .main{
            margin-top: 10px;
            background: url(https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=1065731782,2466636764&fm=26&gp=0.jpg);
            background-size: 100% 100%;
            padding: 10px;
        }
    }
}